// src/utils/index.js
import axios from 'axios'
import request from './request'  // ✅ 默认导入 request 实例

export { request }  // ✅ 命名导出 request，供其他文件引用

// getFullImageUrl 函数保持不变
export const getFullImageUrl = (url) => {
  if (!url || typeof url !== 'string' || ['undefined', 'null', ''].includes(url.trim())) {
    return require('@/assets/default-product.png')
  }
  if (url.startsWith('http://') || url.startsWith('https://')) return url
  if (url.startsWith('/static')) return (axios.defaults.baseURL || 'https://web-production-85aa.up.railway.app') + url
  if (url.startsWith('/img') || url.startsWith('img/')) return url.startsWith('/') ? url : '/' + url
  return `/img/${url}`
}
