<template>
  <div class="app-header">
    <div class="container">
      <div class="header-content">
        <div class="logo">
          <router-link to="/">
            <img :src="logoUrl" alt="绿野鲜踪电商交易平台" class="logo-image" />
            <span class="logo-text">绿野鲜踪电商交易平台</span>
          </router-link>
        </div>
        
        <!-- 移动端菜单按钮 -->
        <div class="mobile-menu-toggle" @click="toggleMobileMenu">
          <el-icon><Menu /></el-icon>
        </div>
        
        <div class="nav-menu" :class="{ 'mobile-active': mobileMenuVisible }">
          <el-menu mode="horizontal" :router="true" :default-active="activeIndex">
            <el-menu-item index="/">首页</el-menu-item>
            <el-menu-item index="/products">产品中心</el-menu-item>
            <el-menu-item index="/custom">知识科普</el-menu-item>
            <el-menu-item index="/about">关于我们</el-menu-item>
          </el-menu>
        </div>
        
        <div class="user-actions">
          <!-- 地址图标 -->
          <div class="action-icon" @click="$router.push('/address')">
            <el-tooltip content="地址管理" placement="bottom">
              <el-icon><Location /></el-icon>
            </el-tooltip>
          </div>

          <!-- 订单图标 -->
          <div class="action-icon" @click="$router.push('/orders')">
            <el-tooltip content="我的订单" placement="bottom">
              <el-icon><Document /></el-icon>
            </el-tooltip>
          </div>
          
          <!-- 购物车图标 -->
          <div class="action-icon" @click="$router.push('/cart')">
            <el-tooltip content="购物车" placement="bottom">
              <el-badge :value="cartCount" :hidden="cartCount === 0">
                <img src="@/assets/cart-icon.png" alt="购物车" class="cart-icon-img" />
              </el-badge>
            </el-tooltip>
          </div>
          
          <!-- 未登录状态 -->
          <template v-if="!isAuthenticated">
            <el-button link @click="$router.push('/login')">登录</el-button>
            <el-button type="primary" @click="$router.push('/register')">注册</el-button>
          </template>
          
          <!-- 已登录状态 -->
          <template v-else>
            <el-dropdown trigger="click" tabindex="0" @visible-change="handleDropdownVisibility">
              <span class="el-dropdown-link">
                <el-avatar size="small" :src="userAvatar" class="user-avatar">{{ userInitials }}</el-avatar>
                <span class="username">{{ currentUser.username }}</span>
              </span>
              <template #dropdown>
                <el-dropdown-menu>
                  <el-dropdown-item @click="logout">
                    <el-icon><SwitchButton /></el-icon> 退出登录
                  </el-dropdown-item>
                </el-dropdown-menu>
              </template>
            </el-dropdown>
          </template>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { computed, ref, onMounted } from 'vue'
import { useStore } from 'vuex'
import { useRoute, useRouter } from 'vue-router'
import { 
  Document, 
  User, 
  Tickets, 
  Location, 
  Setting, 
  SwitchButton,
  Menu
} from '@element-plus/icons-vue'

export default {
  name: 'AppHeader',
  components: {
    Document,
    User,
    Tickets,
    Location,
    Setting,
    SwitchButton,
    Menu
  },
  setup() {
    const store = useStore()
    const route = useRoute()
    const router = useRouter()
    
    const logoUrl = ref(require('@/assets/logo.png'))
    const activeIndex = computed(() => route.path)
    const isAuthenticated = computed(() => store.getters['auth/isAuthenticated'])
    const currentUser = computed(() => store.getters['auth/currentUser'])
    const cartCount = computed(() => store.getters['cart/cartCount'])
    const categories = computed(() => store.getters['products/categories'])
    
    // 移动端菜单状态
    const mobileMenuVisible = ref(false)
    
    // 切换移动端菜单
    const toggleMobileMenu = () => {
      mobileMenuVisible.value = !mobileMenuVisible.value
    }
    
    // 用户头像处理
    const userAvatar = computed(() => {
      if (currentUser.value && currentUser.value.avatar) {
        return currentUser.value.avatar
      }
      return ''
    })
    
    // 如果没有头像，显示用户名首字母
    const userInitials = computed(() => {
      if (currentUser.value && currentUser.value.username) {
        return currentUser.value.username.substring(0, 1).toUpperCase()
      }
      return '用'
    })
    
    const logout = () => {
      store.dispatch('auth/logout')
      router.push('/')
    }
    
    onMounted(() => {
      // 检查认证状态
      store.dispatch('auth/checkAuth')
      
      // 获取产品分类
      if (categories.value.length === 0) {
        store.dispatch('products/fetchProducts')
      }
      
      // 监听窗口大小变化，在大屏幕下自动关闭移动菜单
      window.addEventListener('resize', () => {
        if (window.innerWidth > 768 && mobileMenuVisible.value) {
          mobileMenuVisible.value = false
        }
      })
    })
    
    // 处理下拉菜单的可见性变化，解决无障碍性警告
    const handleDropdownVisibility = (visible) => {
      if (!visible) {
        // 当下拉菜单关闭时，确保没有元素保持焦点
        document.activeElement?.blur();
      }
    }
    
    return {
      logoUrl,
      activeIndex,
      isAuthenticated,
      currentUser,
      cartCount,
      categories,
      userAvatar,
      userInitials,
      logout,
      handleDropdownVisibility,
      mobileMenuVisible,
      toggleMobileMenu
    }
  }
}
</script>

