// src/api/cart.js ✅ 修正导入
import request from '@/utils/request'

// 获取购物车列表
export const getCartList = () => request({
  url: '/api/cart/list',
  method: 'get'
})

// 添加商品到购物车
export const addToCart = (data) => request({
  url: '/api/cart/add',
  method: 'post',
  data
})

// 更新购物车商品数量
export const updateCart = (data) => request({
  url: '/api/cart/update',
  method: 'post',
  data
})

// 删除购物车商品
export const deleteCart = (product_id) => request({
  url: `/api/cart/delete/${product_id}`,
  method: 'delete'
})
