<template>
  <el-card class="product-card" shadow="hover" :body-style="{ padding: '0px' }">
    <div class="product-img" @click="goToDetail">
      <img :src="fullImageUrl" :alt="product.name" />
      <div v-if="product.is_custom" class="custom-badge">可定制</div>
    </div>
    <div class="product-info">
      <div class="product-name" @click="goToDetail">{{ product.name }}</div>
      <div class="product-tags">
        <el-tag v-if="product.eco_certification" size="small" type="success">{{ product.eco_certification }}</el-tag>
        <el-tag v-if="product.is_recyclable" size="small" type="info">可回收</el-tag>
        <el-tag v-if="product.carbon_footprint" size="small" type="warning">{{ product.carbon_footprint }}</el-tag>
      </div>
      <div class="product-price">¥{{ product.price.toFixed(2) }}</div>
      <div class="product-actions">
        <el-button type="primary" size="small" @click="goToDetail">查看详情</el-button>
        <el-button type="success" size="small" @click="addToCart" :disabled="product.stock <= 0">
          <i class="el-icon-shopping-cart-2"></i> 加入购物车
        </el-button>
      </div>
    </div>
  </el-card>
</template>

<script>
import { useRouter } from 'vue-router'
import { useStore } from 'vuex'
import { ElMessage } from 'element-plus'
import { computed } from 'vue'
import { getFullImageUrl } from '../utils/index'

export default {
  name: 'ProductCard',
  props: {
    product: {
      type: Object,
      required: true
    }
  },
  setup(props) {
    const router = useRouter()
    const store = useStore()
    
    // 计算完整图片URL
    const fullImageUrl = computed(() => {
      // 依次检查所有可能的图片字段
      if (props.product.image) {
        return props.product.image
      } else if (props.product.image_url) {
        return getFullImageUrl(props.product.image_url)
      } else if (props.product.images && props.product.images.length > 0) {
        return props.product.images[0]
      }
      
      // 默认图片 - 使用相对路径，会被正确解析为相对于public的路径
      return require('@/assets/default-product.png')
    })
    
    // 跳转到产品详情页
    const goToDetail = () => {
      // 使用product_id而不是id，与后端API匹配
      const productId = props.product.product_id || props.product.id
      router.push(`/product/${productId}`)
    }
    
    // 添加到购物车
    const addToCart = () => {
      if (props.product.stock <= 0) {
        ElMessage.warning('该产品已售罄')
        return
      }
      
      store.dispatch('cart/addToCart', {
        product: props.product,
        quantity: 1
      })
      
      ElMessage.success('成功添加到购物车')
    }
    
    return {
      fullImageUrl,
      goToDetail,
      addToCart
    }
  }
}
</script>

