import axios from 'axios'
import { getCartList, addToCart, updateCart, deleteCart } from '@/api/cart'

const state = {
  cartItems: []
}

const getters = {
  cartItems: state => state.cartItems,
  cartTotal: state => {
    return state.cartItems.reduce((total, item) => {
      return total + item.price * item.quantity
    }, 0) 
  },
  cartCount: state => {
    return state.cartItems.reduce((count, item) => count + item.quantity, 0)
  }
}

const mutations = {
  SET_CART_ITEMS(state, items) {
    state.cartItems = items
  },
  CLEAR_CART(state) {
    state.cartItems = []
  }
}

const actions = {
  async fetchCart({ commit }) {
    try {
      const res = await getCartList()
      console.log('✅ 获取购物车数据:', res)
  
      const items = res.map(item => ({
        id: item.product_id,
        name: item.name || '未知商品',  // 如果接口没有返回商品名，暂时写默认
        price: item.price || 0,
        image_url: item.image || require('@/assets/default-product.png'),
        quantity: item.quantity
      }))
  
      commit('SET_CART_ITEMS', items)
    } catch (error) {
      console.error('获取购物车失败:', error)
    }
  }
  ,

  async addToCart({ dispatch }, { product, quantity = 1 }) {
    const data = {
      product_id: product.id || product.product_id,
      quantity
    }
    await addToCart(data)
    dispatch('fetchCart')
  },

  async updateCartItem({ dispatch }, { id, quantity }) {
    await updateCart({ product_id: id, quantity })
    dispatch('fetchCart')
  },

  async removeFromCart({ dispatch }, id) {
    await deleteCart(id)
    dispatch('fetchCart')
  },

  clearCart({ commit }) {
    commit('CLEAR_CART')
  },

  async checkout({ commit, state, rootGetters, dispatch }) {
    try {
      dispatch('setLoading', true, { root: true })
      const user = rootGetters['auth/currentUser']
      if (!user) throw new Error('用户未登录')

      const orderData = {
        user_id: user.id,
        shipping_address: user.address || '',
        total_amount: parseFloat(getters.cartTotal(state)),
        items: state.cartItems.map(item => ({
          product_id: item.id,
          quantity: item.quantity,
          price: item.price
        }))
      }

      const response = await axios.post('/api/orders', orderData)
      commit('CLEAR_CART')
      dispatch('clearError', null, { root: true })
      return response.data
    } catch (error) {
      dispatch('setError', '结账失败。请稍后再试', { root: true })
      throw error
    } finally {
      dispatch('setLoading', false, { root: true })
    }
  }
}

export default {
  namespaced: true,
  state,
  getters,
  mutations,
  actions
}
