import axios from 'axios'

// 初始状态
const state = {
    user: localStorage.getItem('user') ? JSON.parse(localStorage.getItem('user')) : null,
    token: localStorage.getItem('token') || null,
    userIdentifier: localStorage.getItem('userIdentifier') || null
}

// Getters
const getters = {
    isAuthenticated: state => !!state.token,
    currentUser: state => state.user,
    userToken: state => state.token,
    userIdentifier: state => state.userIdentifier,
    isAdmin: state => state.user && state.user.user_type === 'admin'
}

// Mutations
const mutations = {
    SET_USER(state, user) {
        state.user = user
    },
    SET_TOKEN(state, token) {
        state.token = token
    },
    SET_USER_IDENTIFIER(state, identifier) {
        state.userIdentifier = identifier
    },
    CLEAR_AUTH(state) {
        state.user = null
        state.token = null
        state.userIdentifier = null
    }
}

// Actions
const actions = {
    // 登录
    async login({ commit, dispatch }, credentials) {
        try {
            dispatch('setLoading', true, { root: true })
            const response = await axios.post('/api/user/login', credentials)
            console.log('登录响应:', response.data)
            
            // 根据后端API返回的数据结构处理
            if (response.data && response.data.code === 200 && response.data.data) {
                const data = response.data.data
                const userInfo = data.user_info
                const token = data.token
                const userId = data.user_id
                
                // 存储用户数据和令牌
                commit('SET_USER', userInfo)
                commit('SET_TOKEN', token)
                commit('SET_USER_IDENTIFIER', userId)
                
                // 保存到本地存储
                localStorage.setItem('user', JSON.stringify(userInfo))
                localStorage.setItem('token', token)
                localStorage.setItem('userIdentifier', userId)
                
                dispatch('clearError', null, { root: true })
                return { success: true, user: userInfo }
            } else {
                throw new Error(response.data.msg || '登录失败')
            }
        } catch (error) {
            console.error('登录失败:', error)
            const errorMessage = error.response?.data?.msg || error.message || '登录失败，请检查网络连接'
            dispatch('setError', errorMessage, { root: true })
            return { success: false, message: errorMessage }
        } finally {
            dispatch('setLoading', false, { root: true })
        }
    },

    // 注册
    async register({ commit, dispatch }, userData) {
        try {
            dispatch('setLoading', true, { root: true })
            
            // 准备发送的数据，确保用username值作为nickname传递给后端
            const registerData = {
                ...userData,
                nickname: userData.username  // 显式地将username作为nickname传递
            }
            
            const response = await axios.post('/api/user/register', registerData)

            // 如果注册成功，保存标识符
            if (response.data && response.data.code === 200 && response.data.data) {
                if (response.data.data.user_id) {
                    localStorage.setItem('userIdentifier', response.data.data.user_id)
                    commit('SET_USER_IDENTIFIER', response.data.data.user_id)
                }
                
                dispatch('clearError', null, { root: true })
                return { success: true, message: '注册成功' }
            } else {
                throw new Error(response.data.msg || '注册失败')
            }
        } catch (error) {
            console.error('注册失败:', error)
            const errorMessage = error.response?.data?.msg || error.message || '注册失败，请检查网络连接'
            dispatch('setError', errorMessage, { root: true })
            return { success: false, message: errorMessage }
        } finally {
            dispatch('setLoading', false, { root: true })
        }
    },

    // 登出
    logout({ commit }) {
        // 清除状态
        commit('CLEAR_AUTH')

        // 清除本地存储
        localStorage.removeItem('user')
        localStorage.removeItem('token')
        localStorage.removeItem('userIdentifier')

        // 清除购物车
        commit('cart/CLEAR_CART', null, { root: true })

        return true
    },

    // 检查认证状态
    checkAuth({ commit, state }) {
        // 如果本地存储中有用户信息但状态中没有
        const user = localStorage.getItem('user') ? JSON.parse(localStorage.getItem('user')) : null
        const token = localStorage.getItem('token')
        const identifier = localStorage.getItem('userIdentifier')

        if (user && token && !state.user) {
            commit('SET_USER', user)
            commit('SET_TOKEN', token)
            if (identifier) {
                commit('SET_USER_IDENTIFIER', identifier)
            }
            return true
        } else if ((!user || !token) && state.user) {
            // 如果状态中有用户但本地存储中没有
            commit('CLEAR_AUTH')
            return false
        }

        return !!state.user
    }
}

export default {
    namespaced: true,
    state,
    getters,
    mutations,
    actions
}