import axios from 'axios'

// 商品API封装
const productApi = {
    // 获取商品列表
    getList(params = {}) {
        return axios.get('/api/products/list', { params })
    },
    
    // 获取商品详情
    getDetail(productId) {
        return axios.get('/api/products/detail', { 
            params: { product_id: productId }
        })
    },
    
    // 获取商品分类
    getCategories() {
        return axios.get('/api/products/categories')
    }
}

const state = {
    products: [],
    product: null,
    categories: []
}

const getters = {
    allProducts: state => state.products,
    productById: state => id => state.products.find(product => product.product_id === id || product.id === id),
    currentProduct: state => state.product,
    categories: state => state.categories,
    productsByCategory: state => category => {
        if (!category) return state.products
        return state.products.filter(product => product.category === category || product.category_id === category)
    }
}

const mutations = {
    SET_PRODUCTS(state, products) {
        state.products = products
    },
    SET_PRODUCT(state, product) {
        state.product = product
    },
    SET_CATEGORIES(state, categories) {
        state.categories = categories
    }
}

const actions = {
    // 获取所有产品
    async fetchProducts({ commit, dispatch }, params = {}) {
        try {
            // 添加调试日志
            console.log('获取产品列表，参数:', params)
            
            dispatch('setLoading', true, { root: true })
            const response = await productApi.getList(params)
            
            // 检查response.data是否有数据
            if (response.data && response.data.code === 200 && response.data.data && response.data.data.list) {
                console.log('产品列表获取成功:', response.data.data.list.length + '个产品')
                commit('SET_PRODUCTS', response.data.data.list)
                
                // 提取所有类别
                if (!params.category_id) {
                    // 创建类别集合，避免重复
                    const categorySet = new Set()
                    response.data.data.list.forEach(product => {
                        if (product.category) categorySet.add(product.category)
                        if (product.category_id) categorySet.add(product.category_id)
                    })
                    const categories = Array.from(categorySet)
                    commit('SET_CATEGORIES', categories)
                }
                
                return response.data.data.list
            } else {
                // 如果格式不匹配，可能是旧格式或错误
                console.warn('API返回格式不符合预期:', response.data)
                commit('SET_PRODUCTS', [])
                if (!params.category_id) {
                    commit('SET_CATEGORIES', [])
                }
                return []
            }
        } catch (error) {
            console.error('获取产品失败:', error)
            dispatch('setError', '获取产品失败', { root: true })
            commit('SET_PRODUCTS', [])
            throw error
        } finally {
            dispatch('setLoading', false, { root: true })
        }
    },

    // 获取单个产品详情
    async fetchProduct({ commit, dispatch }, productId) {
        try {
            // 添加调试日志
            console.log('获取产品详情，ID:', productId, '类型:', typeof productId)
            
            // 防御性检查
            if (!productId) {
                console.error('产品ID为空')
                commit('SET_PRODUCT', null)
                return null
            }
            
            dispatch('setLoading', true, { root: true })
            const response = await productApi.getDetail(productId)
            
            // 检查response.data是否有数据
            if (response.data && response.data.code === 200 && response.data.data) {
                console.log('产品详情获取成功:', response.data.data)
                commit('SET_PRODUCT', response.data.data)
                return response.data.data
            } else {
                console.warn('API返回格式不符合预期:', response.data)
                commit('SET_PRODUCT', null)
                return null
            }
        } catch (error) {
            console.error('获取产品详情失败:', error)
            dispatch('setError', '获取产品详情失败', { root: true })
            commit('SET_PRODUCT', null)
            throw error
        } finally {
            dispatch('setLoading', false, { root: true })
        }
    },
    
    // 获取产品分类
    async fetchCategories({ commit, dispatch }) {
        try {
            dispatch('setLoading', true, { root: true })
            const response = await productApi.getCategories()
            
            if (response.data && response.data.code === 200 && response.data.data) {
                commit('SET_CATEGORIES', response.data.data)
                return response.data.data
            } else {
                console.warn('API返回格式不符合预期:', response.data)
                commit('SET_CATEGORIES', [])
                return []
            }
        } catch (error) {
            console.error('获取产品分类失败:', error)
            dispatch('setError', '获取产品分类失败', { root: true })
            commit('SET_CATEGORIES', [])
            throw error
        } finally {
            dispatch('setLoading', false, { root: true })
        }
    }
}

export default {
    namespaced: true,
    state,
    getters,
    mutations,
    actions
}